/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
// import { BrowserRouter as Router, Route } from "react-router-dom";
import { Spinner, Form } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../submitButton";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "ValidateEmail" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    validateTokenEmail,
    validationTokenEmail,
    validationTokenEmailResent,
    language,
  } = props.barrelGetters;
  const { setValidateTokenEmail } = props.barrelSetters;
  const { getUrlParam, sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Hooks
    ============================================================================ */

  // setToken(getUrlParam("token"));
  // setEmail(getUrlParam("email"));

  const token = getUrlParam("token");
  const email = getUrlParam("email");

  /**
    * Effects
    ============================================================================ */
  useEffect(() => {
    if (token === null || email === null) return;

    setValidateTokenEmail(
      JSON.stringify({
        token,
        email,
      })
    );
    // eslint-disable-next-line
  }, []);

  const handleSubmit = (data) => {
    // const vte = JSON.parse(validateTokenEmail);
    // if (vte === null) return;
    // const { token, email } = vte;
    // console.log(data, validationTokenEmail);
    sendRequest({
      req: "resend validate email token",
      data: {
        token: validationTokenEmail.token,
        email: validationTokenEmail.email,
        language,
      },
    });
  };

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    // const queryString = window.location.search;
    // const urlParams = new URLSearchParams(queryString);

    return token === null ? (
      <>
        <h4>{t("A verification link has been sent to your email account.")}</h4>
        <hr />
        <p>
          {t(
            "Please click on the link that has just been sent to your email account"
          )}
        </p>
        {validationTokenEmailResent < 5 && (
          <>
            <br />
            <h5>{t("Didn't receive email?")}</h5>

            <SubmitButton
              {...args}
              caption={t("Submit")}
              visible={true}
              marginTop={20}
              request={"resend validate email token"}
              namespace={props.namespace}
              onSubmit={handleSubmit}
            />
          </>
        )}
      </>
    ) : (
      <div className="text-center">
        <Spinner
          as="span"
          animation="border"
          size="sm"
          role="status"
          aria-hidden="true"
          hidden={false}
        />
      </div>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
