import "bootstrap/dist/css/bootstrap.min.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
// import { useTranslation } from "react-i18next";
import Pagination from "react-bootstrap/Pagination";

/**
  * User imports
  ============================================================================ */

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "Pagination",
    size: "md",
    countItems: 0,
    itemsPerPage: 5,
    active: 0, // active page
    onClick: () => {},
    align: "center",
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  const pages = Math.ceil(props.countItems / props.itemsPerPage);

  if (pages < 2) return <></>;

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  // const { t } = useTranslation([props.namespace]);

  const onClick = (number) => {
    // console.log("on page select", number);
    switch (number) {
      case "First":
        number = 1;
        break;
      case "Prev":
        number = props.active > 1 ? props.active - 1 : 1;
        break;
      case "Next":
        number =
          props.active < Math.ceil(props.countItems / props.itemsPerPage)
            ? props.active + 1
            : Math.ceil(props.countItems / props.itemsPerPage);
        break;
      case "Last":
        number = Math.ceil(props.countItems / props.itemsPerPage);
        break;
      default:
        break;
    }
    // getBusinesses(number);
    props.onClick(number);
  };

  const getPages = () => {
    let items = [];
    for (let number = 1; number <= pages; number++) {
      if (
        number === 1 ||
        number === pages ||
        (number >= props.active - 2 && number <= props.active + 2)
      ) {
        items.push(
          <Pagination.Item
            key={number}
            active={number === props.active}
            onClick={() => onClick(number)}
          >
            {number}
          </Pagination.Item>
        );
      } else if (number === 2 || number === pages - 1) {
        items.push(<Pagination.Ellipsis key={number} disabled />);
      }
    }
    return items;
  };

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    const { align } = props;

    return (
      <div
        style={{
          textAlign: `${align}`,
          // backgroundColor: "red",
        }}
      >
        <span style={{ display: "inline-block" }}>
          <Pagination size={props.size} style={{ padding: 0, margin: 0 }}>
            {pages > 5 && (
              <>
                <Pagination.First onClick={() => props.onClick("First")} />
                <Pagination.Prev onClick={() => props.onClick("Prev")} />
              </>
            )}

            {getPages()}

            {pages > 5 && (
              <>
                <Pagination.Next onClick={() => props.onClick("Next")} />
                <Pagination.Last onClick={() => props.onClick("Last")} />
              </>
            )}
          </Pagination>
        </span>
      </div>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
