import React, { useEffect } from "react";
import {
  Accordion,
  Card,
  Tab,
  Row,
  Col,
  ListGroup,
  // Nav,
  // Form,
  // Toast,
  // Badge,
  // Container,
  // Button,
} from "react-bootstrap";
// import { useAccordionToggle } from "react-bootstrap/AccordionToggle";
import { useTranslation } from "react-i18next";
// import Octicon, { Plus, PlusSmall, Gear } from "@primer/octicons-react";
import { Add, Extension, Store } from "@material-ui/icons";

// import Dropzone from "../dropzone";

import BusinessRegistration from "../businessRegistration";
import PurposeRegistration from "../purposeRegistration";
import ShareholderRegistration from "../shareholderRegistration";
// import shareholderRegistration from "../shareholderRegistration";
import Shareholder from "../shareholder";
import Pagination from "../pagination";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = { namespace: "MyBusiness" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("TRANSFER DATA", props);

  // const [businessLoaded, setBusinessLoaded] = useState(null);

  /**
   * Barrels
   */
  const {
    userBusinesses,
    countBusinesses,
    businessesPerPage,
    activePageBusinesses,
    userBusinessesLoaded,
    activeBusiness,
    activeBusinessID,
    businessPurposes,
    countBusinessPurposes,
    businessPurposesPerPage,
    activePageBusinessPurposes,
    activeBusinesPurpose,
    purposeShareholders,
    language,
    accountNumber,
  } = props.barrelGetters;
  const { setRedirectTo } = props.barrelSetters;
  const {
    getBusinesses,
    getBusinessPurposes,
    getPurposeShareholders,
  } = props.barrelFunctions;
  // const {  } = props.barrelEventHandlers;

  const { t } = useTranslation([props.namespace]);

  useEffect(() => {
    // getBusinesses();
    // getBusinesses();
    // eslint-disable-next-line
  }, []);

  /**
   * Toggle business (Accordion card)
   * @param {*} param0
   */
  // const ToggleBusiness = ({ children, eventKey, businessID }) => {
  //   const decoratedOnClick = useAccordionToggle(eventKey, () =>
  //     getBusinessPurposes(businessID, eventKey)
  //   );

  //   return <div onClick={decoratedOnClick}>{children}</div>;
  // };
  const ToggleBusiness = ({ eventKey, businessID }) => {
    getBusinessPurposes(businessID, eventKey, 1);
  };

  // const onPageSelect = (number) => {
  //   // console.log("on page select", number);
  //   switch (number) {
  //     case "First":
  //       number = 1;
  //       break;
  //     case "Prev":
  //       number = activePageBusinesses > 1 ? activePageBusinesses - 1 : 1;
  //       break;
  //     case "Next":
  //       number =
  //         activePageBusinesses < Math.ceil(countBusinesses / businessesPerPage)
  //           ? activePageBusinesses + 1
  //           : Math.ceil(countBusinesses / businessesPerPage);
  //       break;
  //     case "Last":
  //       number = Math.ceil(countBusinesses / businessesPerPage);
  //       break;
  //     default:
  //       break;
  //   }
  //   getBusinesses(number);
  // };

  // const onPurposesPageClick = (number) => {
  //   switch (number) {
  //     case "First":
  //       number = 1;
  //       break;
  //     case "Prev":
  //       number =
  //         activePageBusinessPurposes > 1 ? activePageBusinessPurposes - 1 : 1;
  //       break;
  //     case "Next":
  //       number =
  //         activePageBusinessPurposes <
  //         Math.ceil(countBusinessPurposes / businessPurposesPerPage)
  //           ? activePageBusinessPurposes + 1
  //           : Math.ceil(countBusinessPurposes / businessPurposesPerPage);
  //       break;
  //     case "Last":
  //       number = Math.ceil(countBusinessPurposes / businessPurposesPerPage);
  //       break;
  //     default:
  //       break;
  //   }
  //   getBusinessPurposes(activeBusinessID, 0, number);
  // };

  /**
   * Toggle purpose (Accordion card)
   * @param {*} param0
   */
  // const TogglePurpose = ({ children, eventKey, purposeID }) => {
  //   const decoratedOnClick = useAccordionToggle(eventKey, () =>
  //     // getBusinessPurposes(businessID, eventKey)

  //     getPurposeShareholders(purposeID, eventKey)
  //   );

  //   return <div onClick={decoratedOnClick}>{children}</div>;
  // };

  const TogglePurpose = ({ eventKey, purposeID }) => {
    getPurposeShareholders(purposeID, eventKey);
  };

  /**
   * Toggle shareholders (Accordion card)
   * @param {*} param0
   */
  // const ToggleShareholders = ({ children, eventKey, purposeID }) => {
  //   const decoratedOnClick = useAccordionToggle(eventKey, () =>
  //     getPurposeShareholders(purposeID, eventKey)
  //   );

  //   return <div onClick={decoratedOnClick}>{children}</div>;
  // };

  /**
   * Create:
   * - business menu
   * - business purposes menu
   */
  let menuItems = [];
  let purposes = [];
  let countShareholders = 0;
  userBusinesses !== null &&
    Object.keys(userBusinesses).map((business, keyBusiness) => {
      menuItems[keyBusiness] = [];
      purposes[keyBusiness] = [];
      businessPurposes !== null &&
        Object.keys(businessPurposes).map((purpose, keyPurpose) => {
          if (
            businessPurposes[purpose].business_id ===
            userBusinesses[business].business_id
          ) {
            menuItems[keyBusiness][keyPurpose] = (
              <ListGroup.Item
                action
                key={businessPurposes[purpose].purpose_id}
                eventKey={businessPurposes[purpose].purpose_id}
                onClick={() =>
                  TogglePurpose({
                    eventKey: keyPurpose,
                    purposeID: businessPurposes[purpose].purpose_id,
                  })
                }
              >
                <span style={{ whiteSpace: "nowrap" }}>
                  <Extension style={{ color: "var(--dark)" }} />{" "}
                  {businessPurposes[purpose].title}
                </span>
              </ListGroup.Item>
            );

            purposes[keyBusiness][keyPurpose] = (
              <Tab.Pane
                key={businessPurposes[purpose].purpose_id}
                eventKey={businessPurposes[purpose].purpose_id}
              >
                <Accordion defaultActiveKey="shareholders">
                  {/* Purpose ID */}
                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "pointer" }}
                      eventKey="purpose_id"
                    >
                      {t("Purpose ID")}
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="purpose_id">
                      <Card.Body>
                        <Card.Text>
                          {businessPurposes[purpose].purpose_id}
                        </Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                  {/* Purpose description */}
                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "pointer" }}
                      eventKey="description"
                    >
                      {t("Purpose Description")}
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="description">
                      <Card.Body>
                        <Card.Text>
                          {businessPurposes[purpose].description}
                        </Card.Text>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "pointer" }}
                      eventKey="shareholders"
                    >
                      {t("Purpose Shareholders")}
                    </Accordion.Toggle>

                    {/* Purpose shareholders */}
                    <Accordion.Collapse eventKey="shareholders">
                      <Card.Body>
                        {typeof purposeShareholders !== "undefined" &&
                          purposeShareholders !== null &&
                          Object.keys(purposeShareholders).map(
                            (shareholder, keyShareholder) => {
                              if (
                                purposeShareholders[shareholder].purpose_id ===
                                businessPurposes[purpose].purpose_id
                              ) {
                                const { purpose_id } = purposeShareholders[
                                  shareholder
                                ];
                                const {
                                  shareholder_id,
                                  firstname,
                                  lastname,
                                  // email,
                                  roles,
                                  share,
                                } = purposeShareholders[shareholder];

                                countShareholders++;

                                const arrRoles =
                                  roles !== null ? roles.split(",") : [];

                                return (
                                  <Shareholder
                                    {...args}
                                    key={keyShareholder}
                                    id={keyShareholder}
                                    shareholder={shareholder_id}
                                    purpose={purpose_id}
                                    firstname={firstname}
                                    lastname={lastname}
                                    share={share}
                                    roles={arrRoles}
                                  />
                                );
                              } else {
                                return ""; // TODO: check if this works
                              }
                            }
                          )}
                        {countShareholders === 0 && (
                          <Card.Text className="text-muted">
                            {t("No shareholders")}
                          </Card.Text>
                        )}

                        {/* End shareholders list */}
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>

                  <Card bg="light">
                    <Accordion.Toggle
                      as={Card.Header}
                      style={{ cursor: "pointer" }}
                      eventKey="2"
                    >
                      <span style={{ whiteSpace: "nowrap" }}>
                        <Add
                          fontSize="small"
                          style={{ color: "var(--dark)" }}
                        />
                        &nbsp;{t("Add Shareholder")}
                      </span>
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="2">
                      <Card.Body>
                        <ShareholderRegistration
                          {...args}
                          purposeID={businessPurposes[purpose].purpose_id}
                        />
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                </Accordion>
              </Tab.Pane>
            );
          }
          countShareholders = 0;
          return true;
        });
      return true;
    });

  const jsx = (
    <>
      <Accordion
        defaultActiveKey={
          activeBusiness !== null || !userBusinessesLoaded
            ? activeBusiness
            : "myBusinesses"
        }
      >
        {/* Add business */}
        <Card>
          <Accordion.Toggle
            as={Card.Header}
            eventKey="addBusiness"
            style={{ cursor: "pointer" }}
          >
            <span>
              <Add fontSize="small" style={{ color: "var(--dark)" }} />
              &nbsp;
              {t("Add Business")}
            </span>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="addBusiness">
            <Card.Body>
              <BusinessRegistration {...args} />
            </Card.Body>
          </Accordion.Collapse>
        </Card>

        {/* List user businesses */}
        {userBusinesses !== null &&
          Object.keys(userBusinesses).map((business, keyBusiness) => {
            const date = new Date(userBusinesses[business].created);
            const businessSince = new Intl.DateTimeFormat(language).format(
              date
            );
            // keyBusiness[3] = userBusinesses[business].business_id;
            return (
              <Card key={keyBusiness}>
                <Accordion.Toggle
                  as={Card.Header}
                  eventKey={keyBusiness}
                  style={{ cursor: "pointer" }}
                  onClick={() =>
                    ToggleBusiness({
                      eventKey: keyBusiness,
                      businessID: userBusinesses[business].business_id,
                    })
                  }
                >
                  <Store style={{ color: "var(--dark)" }} />{" "}
                  {userBusinesses[business].title}
                </Accordion.Toggle>
                <Accordion.Collapse eventKey={keyBusiness}>
                  <Card.Body>
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey={
                        activeBusinesPurpose !== null
                          ? activeBusinesPurpose
                          : "aboutBusiness"
                      }
                    >
                      <Row>
                        {/* Layout: Left side - Menu */}
                        <Col sm={5}>
                          <ListGroup variant="flush">
                            {/* Menu item: About business */}
                            <ListGroup.Item action eventKey="aboutBusiness">
                              {t("About")}
                            </ListGroup.Item>

                            {/* Menu item: Add purpose */}
                            <ListGroup.Item action eventKey="addPurpose">
                              <span style={{ whiteSpace: "nowrap" }}>
                                <Add
                                  fontSize="small"
                                  style={{ color: "var(--dark)" }}
                                />
                                &nbsp;{t("Add Purpose")}
                              </span>
                            </ListGroup.Item>

                            {/* Menu items: Registered purposes */}
                            {menuItems[keyBusiness]}

                            {/* old add purpose */}
                          </ListGroup>
                          {countBusinessPurposes !== null && (
                            <>
                              <br />
                              <Pagination
                                {...args}
                                size="sm"
                                countItems={countBusinessPurposes}
                                itemsPerPage={businessPurposesPerPage}
                                active={activePageBusinessPurposes}
                                onClick={(number) =>
                                  getBusinessPurposes(
                                    activeBusinessID,
                                    0,
                                    number
                                  )
                                }
                              />
                            </>
                          )}
                        </Col>

                        {/* Layout: Right side - Content */}
                        <Col sm={7}>
                          <Tab.Content>
                            <Tab.Pane eventKey="aboutBusiness">
                              <Accordion defaultActiveKey="businessID">
                                {/* Business logo */}

                                {/*
                              <Card>
                                <Accordion.Toggle
                                  as={Card.Header}
                                  style={{ cursor: "pointer" }}
                                  eventKey="businessLogo"
                                >
                                  [LOGO]
                                </Accordion.Toggle>
                                <Accordion.Collapse eventKey="businessLogo">
                                  <Card border="light">
                                    <Card.Body>
                                      <Dropzone data={args} />
                                    </Card.Body>
                                  </Card>
                                </Accordion.Collapse>
                              </Card>
                              */}

                                {/* Business ID */}
                                <Card>
                                  <Accordion.Toggle
                                    as={Card.Header}
                                    style={{ cursor: "pointer" }}
                                    eventKey="businessID"
                                  >
                                    {t("Business ID")}
                                  </Accordion.Toggle>
                                  <Accordion.Collapse eventKey="businessID">
                                    <Card border="light">
                                      <Card.Body>
                                        <Card.Text>
                                          {userBusinesses[business].business_id}
                                        </Card.Text>
                                      </Card.Body>
                                    </Card>
                                  </Accordion.Collapse>
                                </Card>

                                {/* Business description */}
                                <Card>
                                  <Accordion.Toggle
                                    as={Card.Header}
                                    style={{ cursor: "pointer" }}
                                    eventKey="businessDescription"
                                  >
                                    {t("Business Description")}
                                  </Accordion.Toggle>
                                  <Accordion.Collapse eventKey="businessDescription">
                                    <Card border="light">
                                      <Card.Body>
                                        <Card.Text>
                                          {userBusinesses[business].description}
                                        </Card.Text>
                                      </Card.Body>
                                    </Card>
                                  </Accordion.Collapse>
                                </Card>

                                {/* Business Creation Date */}
                                <Card>
                                  <Accordion.Toggle
                                    as={Card.Header}
                                    style={{ cursor: "pointer" }}
                                    eventKey="businessCreationDate"
                                  >
                                    {t("Business Created")}
                                  </Accordion.Toggle>
                                  <Accordion.Collapse eventKey="businessCreationDate">
                                    <Card border="light">
                                      <Card.Body>
                                        <Card.Text>{businessSince}</Card.Text>
                                      </Card.Body>
                                    </Card>
                                  </Accordion.Collapse>
                                </Card>
                              </Accordion>
                            </Tab.Pane>

                            {/* Start Business purposes */}
                            {purposes[keyBusiness]}
                            {/* End Business purposes */}

                            {/* Add new purpose */}
                            <Tab.Pane eventKey="addPurpose">
                              <Accordion defaultActiveKey="newPurpose">
                                <Card>
                                  <Accordion.Toggle
                                    as={Card.Header}
                                    eventKey="newPurpose"
                                  >
                                    {t("New Purpose")}
                                  </Accordion.Toggle>
                                  <Accordion.Collapse eventKey="newPurpose">
                                    <Card.Body>
                                      <PurposeRegistration
                                        data={args}
                                        businessID={
                                          userBusinesses[business].business_id
                                        }
                                      />
                                    </Card.Body>
                                  </Accordion.Collapse>
                                </Card>
                              </Accordion>
                            </Tab.Pane>
                          </Tab.Content>
                        </Col>
                      </Row>
                    </Tab.Container>
                    {/* pagination prev */}
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
            );
            // console.log("BUSINESS:", userBusinesses[business]);
          })}

        {/* Get business */}

        {false && userBusinesses === null && (
          <Card>
            <Accordion.Toggle
              as={Card.Header}
              eventKey="myBusinesses"
              onClick={getBusinesses}
              style={{ cursor: "pointer" }}
            >
              <span>{t("My Businesses")}</span>
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="myBusinesses">
              <Card.Body>{t("No businesses")}</Card.Body>
            </Accordion.Collapse>
          </Card>
        )}

        {/* old add business */}
      </Accordion>
      {userBusinesses !== null && (
        <>
          <br />

          <Pagination
            {...args}
            size="md"
            countItems={countBusinesses}
            itemsPerPage={businessesPerPage}
            active={activePageBusinesses}
            onClick={(number) => getBusinesses(number)}
          />
        </>
      )}
    </>
  );

  if (accountNumber === null) setRedirectTo("/sign-out");

  return <>{jsx}</>;
};
