/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Button, Modal, Navbar, Nav } from "react-bootstrap";
import ErrorBoundary from "../errorBoundary";

/**
  * User imports
  ============================================================================ */
// import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "ModalMessage",
    centered: true,
    message: "",
    title: null,
    footer: null,
    // bodyStyle: { padding: "1rem" },
    onHide: () => {},
    size: "sm",
    file: null,
    backdrop: true, // "static"
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const ExternalFile =
    props.file !== null
      ? React.lazy(() => import(`../locale/${props.file}`))
      : "";

  // async function loadExternalFile() {
  //   await React.preloadLazy(ExternalFile);
  // }

  // loadExternalFile();

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return (
      <Modal
        {...props}
        size={props.size}
        aria-labelledby="contained-modal-title-vcenter"
        centered={props.centered}
        backdrop={props.backdrop}
      >
        {props.title !== null && (
          <Modal.Header closeButton>
            <Navbar
              // className="card-header"
              // className="navbar-trans"
              // bg="light"
              // variant="light"
              style={{
                borderRadius: "5px",
                background: "transparent !important",
                // color: "#f7f7f7",
                backgroundColor: "", // e8f6f3
                boxShadow: "none",
                // whiteSpace: "nowrap",
              }}
            >
              <Navbar.Brand href="/">
                <img
                  src="gfx/zlogo_light.png"
                  width="auto"
                  height="30"
                  className="d-inline-block align-top"
                  alt={t("Logo")}
                />
              </Navbar.Brand>
              <Nav className="mr-auto">
                <Modal.Title id="contained-modal-title-vcenter">
                  {props.title}
                </Modal.Title>
              </Nav>
            </Navbar>
          </Modal.Header>
        )}
        <Modal.Body
          style={{ maxHeight: "calc(100vh - 210px)", overflowY: "auto" }}
        >
          {props.message !== "" ? (
            props.message
          ) : (
            <ErrorBoundary>
              <ExternalFile />
            </ErrorBoundary>
          )}
        </Modal.Body>
        {props.footer !== null && (
          <Modal.Footer>
            <Button block onClick={props.onHide}>
              {t("Close")}
            </Button>
          </Modal.Footer>
        )}
      </Modal>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
